.cass-knowledge-search{
    position: relative;
    top: -$measure-large;

    .news-finder__form .news-articles-list {
        display: block;
        width: 100%;
        padding-left: $measure-medium;
    
        li{
            display: block;
            //background: $color-white;
            margin-bottom: $measure-large;
            width: 100%;
        }
    }
    .news-card{
        //display: flex;
       // flex-direction: row;
        position: relative;
        overflow: hidden;
        display: grid;
        grid-template-columns: repeat(16, 1fr);
        grid-column-gap: 0.8rem;

        @include media('<large'){
            //background: red;
        }
    }

    .news-card .image-credit{
        left: 0;
        right: inherit;
    }
    .news-card-media{
        max-height: 200px;
        grid-column: 1 / 6;

        @include media('<large'){
            grid-column: 1 / 8;
            position: absolute;
        }
    }

    .news-card-media img{
        min-height: 250px;
        //min-width: 350px;
        object-fit: cover;
        width: 100%;
    }
    
    .news-card-text{
        height: 90%;
        left: -10%;
        position: relative;
        background: $color-white;
        padding: $measure-medium;
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
        grid-column: 6 / -1;
        width: 110%;
        margin-bottom: $measure-medium;

        @include media('<large'){
            grid-column: 6 / -1;
        }
    }

    .news-card-text .item__date{
        color: $color-lucifer;
        margin-bottom: $measure-small;
        display: block;
    }
    .news-card-text-label{
        color: $color-emperor;
    }

    .news-card-text-label:before{
        content: '\f15c';
        font-family: FontAwesome;
        margin-right: $measure-medium;
    }

    .news-card-content {
        h3{
            line-height: 1.2;
            color: $color-emperor;
            
        }
    }
    .news-card-series-wrap{
        
    }

    .news-card-series{
        border: $border-width-hairline $border-style-default $color-quill;
        padding-left: $measure-small;
        padding-right: $measure-small;
        color: $color-alizarin;
        margin-right: $measure-medium;
    }
    .news-card-topic__wrap{
        display: inline-block;
    }

    .news-card-topic{
        border: $border-width-hairline $border-style-default $color-quill;
        padding-left: $measure-small;
        padding-right: $measure-small;
        color: $color-emperor;
        margin-top: $measure-small;
        display: inline-block;
    }
    
}