.news-events-landing{

    .news-articles-list{
        width: 100%;
    }

    .featured-events{
        width: 100%;
    }

    .featured-events .grid--thirds {
        width: 100%;

        li {
            list-style: none;
        }
        
    }
    .featured-event{
        padding-left: $measure-small;
        padding-right: $measure-small;
    }
    .featured-item__image__wrap{
        position: relative;
        padding-bottom: 60%;
        overflow-y: hidden;
    }

    .featured-item__image{
        position: absolute;
        top: 0;
        left: 0;

        img{
            width: 100%;
        }
    }

    .featured-item__content{
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
        background-color: $color-white;
        position: relative;
        top: -40px;
        width: 90%;
        //padding: $measure-medium;
        display: flex;
        flex-direction: row;
    }

    .featured-item__content__date__wrap{
        background: $color-alizarin;
        width: 20%;
        text-align: center;
        color: $color-white;
        display: flex;
        flex-direction: column;

        time {
            margin-top: auto;
            margin-bottom: auto;
        }
    }

    .featured-item__content__details__wrap{
        padding: $measure-medium;
    }

    .featured-item__content__details__time{

        &:before{
            content: '\f017';
            font-family: FontAwesome;
            margin-right: $measure-small;
            margin-bottom: $measure-small;
        }
    }
    .featured-events__container__cta__wrap{
        display: flex;
        flex-direction: row-reverse;
    }
    .featured-events__container__cta{

        &:after{
            font-family: 'FontAwesome';
            content: '\f061';
            margin-left: 1rem;
        }
        
    }
    .promo{
        width: 100%;
        color: $color-white;
        position: relative;
        margin-top: $measure-large;
        
        &:before{
            content: "";
            width: 100%;
            position: absolute;
            height: 60%;
            background: $color-prussian;
        }
    }

    .promo__container__wrap{
        margin: $measure-xlarge $measure-large;
        position: relative;
    }

    .promo__container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);

    }

    .promo__container__image{
        grid-column: 1 / 2;
    }

    .promo__container__text{
        grid-column: 2 / 3;
        background: $color-white;
        color: $color-black;
        padding: $measure-large;
        
    }

    .promo_container__text_subtitle{
        font-size: $font-size-gamma;
        margin-bottom: $measure-medium;
    }

    .promo__twitter-tag{
        color: $color-black;
        margin-top: $measure-medium;

        .fa-twitter{
            font-family: "fontAwesome"
            
        }
    }

    .footer-meta::before {
        background: $color-concrete;
        left: 0;
        transform: inherit;
        
    }

    
    
}

.timeline-Tweet-text, .timeline-Tweet{
    background: green;
}

